import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../helper/function";
import { MenuService } from "../../../services/adminService";
import { MyFormItem } from "../../core/Input";
import { MyButton } from "../../core/MyButton";
import SearchComponent from "../../core/SearchComponent";

export default function ModifyMenuComponent(props: any) {

    const [dataSearch, setDataSearch] = useState([]);
    const [parentKey, setParentKey] = useState(null);
    const [parentName, setParentName] = useState(null);
    const [title, setTitle] = useState(null);
    const [route, setRoute] = useState(null);
    const { setIsLoading } = props;
    const loadData = useCallback(() => {
        if (props.keyData === undefined) {
            return;
        }

        setIsLoading(true);
        MenuService.GetDetail(props.keyData, (data: any) => {
            let row = data.data.data;
            setTitle(row.title);
            setRoute(row.route);
            if (row.parent !== null) {
                setParentKey(row.parent.key);
                setParentName(row.parent.title + (" - ") as string | undefined + row.parent.route);
            }
        }, undefined, () => {
            setIsLoading(false);
        });
    }, [props.keyData, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleOnClose() {
        props.setVisible(false);
        if (props.keyData !== undefined) {
            props.setKeyData(undefined);
        }
        setTitle(null);
        setRoute(null);
        setParentKey(null);
        setParentName(null);
    }

    function handleSearch(value: string) {
        props.setIsLoading(true);
        MenuService.Query(value, (data: any) => {
            setDataSearch(data.data.data);
        }, undefined, () => {
            props.setIsLoading(false);
        });
    }

    function handleSubmitForm() {
        var body = {
            title: title,
            route: route,
            parentKey: parentKey === undefined || parentKey === '' || parentKey === null ? null : parentKey
        };

        props.setIsLoading(true);
        if (props.keyData === undefined) {
            add(body);
        } else {
            edit(body);
        }
    }

    function add(body: any) {
        MenuService.Add(body, (data: any) => {
            Fn.PrintMsgSuccess(data);
            props.loadData();
        }, () => {
            props.setIsLoading(false);
        })
    }

    function edit(body: any) {
        MenuService.GetDetail(props.keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            MenuService.Edit(props.keyData, body, (data: any) => {
                Fn.PrintMsgSuccess(data);
                props.loadData();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    return (
        <Drawer
            open={props.visible}
            title={props.keyData === undefined ? "Thêm mới menu" : "Chỉnh sửa menu"}
            onClose={handleOnClose}>
            <Form
                layout="vertical">
                <MyFormItem.Input
                    label="Title"
                    value={title}
                    setValue={setTitle} />
                <MyFormItem.Input
                    label="Route"
                    value={route}
                    setValue={setRoute} />
                <SearchComponent
                    label="Parent"
                    onSearch={handleSearch}
                    dataSearch={dataSearch}
                    resultKey={parentKey}
                    resultName={parentName}
                    setResultKey={setParentKey}
                    setResultName={setParentName}
                    isLoading={props.isLoading} />
                <MyButton
                    value={props.keyData === undefined ? "Thêm mới" : "Chỉnh sửa"}
                    type="success"
                    isLoading={props.isLoading}
                    onClick={handleSubmitForm} />
            </Form>
        </Drawer>
    );
}