import { Popconfirm } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../helper/function";
import { TableColumn } from "../../../models/baseModel";
import { MenuService } from "../../../services/adminService";
import { MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../core/TableModel";
import TableComponent from "../../core/TableNewComponent";
import ModifyMenuComponent from "./ModifyComponent";

export default function ManageMenuIndexComponent() {
    const column: Array<TableColumn> = [
        new TableColumn({ title: "STT", dataIndex: "stt", key: "stt", disabled: true }),
        new TableColumn({ title: "Title", dataIndex: "title", key: "title" }),
        new TableColumn({ title: "Route", dataIndex: "route", key: "route" }),
        new TableColumn({ title: "Parent", dataIndex: "parent", key: "parentTitle" }),
        new TableColumn({
            title: "Action", key: "action", disabled: true, render: (data: any, row: any) => {
                return (
                    <div>
                        <MyButton
                            type="link"
                            value="Chỉnh sửa"
                            onClick={() => {
                                handleEdit(row.key);
                            }} />
                        <Popconfirm
                            title="Bạn có muốn xóa không?"
                            onConfirm={() => {
                                handleDelete(row.key);
                            }}>
                            <MyButton
                                type="link"
                                value="Xóa" />
                        </Popconfirm>
                    </div>
                )
            }
        }),
    ];

    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>(undefined);

    const loadData = useCallback(() => {
        setIsLoading(true);
        MenuService.GetList(pageInfo.pageNumber, pageInfo.pageSize, orderBy, (data: any) => {
            setPageInfo(new PageInfoModel(data));
            var row = data.data.data.pagedData.map((data: any, index: number) => {
                return {
                    stt: index + 1,
                    key: data.key,
                    title: data.title,
                    route: data.route,
                    parent: data.parent === null ? '*** Root ***' : data.parent.title
                };
            });
            setData(row);
        }, undefined, () => {
            setIsLoading(false);
        })
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData]);


    function handleClickAdd() {
        setVisible(true);
    }

    function handleEdit(key: string) {
        setVisibleEdit(true);
        setKeyEdit(key);
    }

    function handleDelete(key: string) {
        setIsLoading(true);
        MenuService.GetDetail(key, (data: any) => {
            MenuService.Delete(key, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                loadData();
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }

    function RenderButtonList() {
        return (<MyButton
            type="primary"
            value="Thêm mới"
            onClick={handleClickAdd} />
        );
    }

    return (
        <MyBox
            title="Quản lý menu"
            isLoading={isLoading}
            buttonList={RenderButtonList}>

            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData}
                orderBy={orderBy}
                setOrderBy={setOrderBy} />

            <ModifyMenuComponent
                visible={visible}
                setVisible={setVisible}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />

            <ModifyMenuComponent
                keyData={keyEdit}
                setKeyData={setKeyEdit}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />
        </MyBox>
    );
}